export default class W3Util {
    constructor(web3) {
        this.web3 = web3;
    }
    estimateGas(opts) {
        return new Promise((resolve, reject) => {
            this.web3.eth.estimateGas(opts, (e, r) => {
                if (e)
                    reject(e);
                else
                    resolve(r);
            });
        });
    }
    networkGasPrice() {
        return new Promise((resolve, reject) => {
            this.web3.eth.getGasPrice((e, r) => {
                if (e)
                    reject(e);
                else
                    resolve(r);
            });
        });
    }
    getReceipt(txHash) {
        return new Promise((resolve, reject) => {
            this.web3.eth.getTransactionReceipt(txHash, (e, r) => {
                if (e)
                    reject(e);
                else
                    resolve(r);
            });
        });
    }
}
